import React, { useState, useEffect } from 'react';
import IconScroll from "./IconScroll";

export default function Welcome(props) {
    const [balling, setBalling] = useState(11);

    const data = props.data;
    const gotoPage = (pageId) => {
        var elmnt = document.getElementById(pageId);
        elmnt.scrollIntoView({ behavior: 'smooth' });
    }
    return (
        <div>
            <div className="cont">
                <main className="content">
                    <div className="left">
                        <div dangerouslySetInnerHTML={{ __html: data['welcome-message-1'] }} >
                        </div>
                        <div className="divider"></div>
                        <div dangerouslySetInnerHTML={{ __html: data['welcome-message-2'] }} >
                        </div>
                        <div>
                            <a className="yob" href="#workP">
                                My Projects
                            </a>
                        </div>
                    </div>
                    <div className={"right " + (balling ? "paused" : "")} id={(balling === 11 ? "" : "smashme")}>
                        <div id="moveme" className={"portrait " + (balling ? "paused" : "")} onMouseDown={() => { setBalling(!balling) }}>
                            <div className="overlay">
                                <span>Click me</span>
                            </div>
                            <img id="playme" className={(balling ? "paused" : "")} src={"/assets/me.jpg"} alt="ashraf portrait" />
                        </div>
                    </div>
                </main>
                <div className="bot">
                    <div style={{ width: 100 }}></div>
                    <div className="scroll">
                        <IconScroll color="#fff" iconColor="#f7f749" text="Start scrolling to see more" />
                    </div>
                    <div className="language" style={{ width: 100 }}>
                        {/* <span onClick={() => props.setLang(false)}>èªž Change Language </span> */}
                    </div>
                </div>
            </div>
            <style jsx>{`



@keyframes play {
    100% {transform:rotate(360deg)}
}
@keyframes smash {
0% {transform: scaleY(.7)}
15%  {transform: scaleY(1)}
}
@keyframes move {
0% {transform: translateY(0) ;box-shadow: 20px 20px  10px #000000}
100% {transform: translateY(-350px);box-shadow:20px 350px  150px #000000}
}
#smashme{
animation-name: smash;
animation-duration: 1s;
animation-direction: alternate;
animation-iteration-count: infinite;
animation-timing-function: ease-in-out;
transform-origin: bottom;
}
#moveme{
border-radius: 100%;
animation-name: move;
animation-duration: 1s;
animation-direction: alternate;
animation-iteration-count: infinite;
animation-timing-function: ease-out;
}
// #playme {
//     animation-name: play;
//     animation-duration: 5.3s;
//     animation-iteration-count: infinite;
//     animation-timing-function: linear;
// }
.paused {
    animation-play-state: paused;
}
// /////////////
                .yob{
                    background: #ffff00;
                    color: #010101;
                    border: 2px solid #ffff00;
                    padding: 14px 60px;
                    font-size: 13pt;
                    font-weight: 600;
                    transition-duration: 200ms;
                    cursor: pointer;
                    margin-top: 20px;
                    margin-right: 20px;
                    text-decoration: none;
                    display: block;
                    max-width: 300px;
                    width: fit-content;
                }
                .yob.sm{
                    padding: 5px 20px;
                    font-weight: 500;
                    font-size: 13pt;
                }
                .yob.reverse{
                    background: none;
                    color: #ffff00;
                    border: 2px solid #ffff00;
                }
                .yob:hover{
                    background: #f7f749;
                    border-color: #f7f749;
                }
                .bold{
                    font-weight: bold;
                }
                .cont{
                    background: #111111;
                    height: 100vh;
                    height: -webkit-fill-available;
                    width: 100%;
                    color: #f1f1f1;
                    display:flex;
                    flex-direction: column;
                    font-weight: 300;
                    position: fixed;
                    z-index: -1;
                    top: 0;
                    transition-duration: 1s;
                }
                .bot{
                    margin: 0 auto;
                    display: flex;
                    flex-direction: row;
                    justify-content: space-between;
                    width: 90%;
                }
                @keyframes breathing {
                  0% {
                    transform: scale(1);
                  }
                
                  25% {
                    transform: scale(1.1);
                  }
                
                  60% {
                    transform: scale(1);
                  }
                
                  100% {
                    transform: scale(1);
                  }
                }
                .scroll{
                    margin-bottom: 20px;
                    animation: breathing 3s ease-out infinite normal;
                }
                .bot .language{
                    display: flex;
                    align-items: center;
                    cursor: pointer;
                    font-weight: bold;
                    color: #ffff00;
                }
                .content{
                    width: 70%;
                    margin: auto;
                    display: grid;
                    grid-template-columns: auto auto;
                    grid-gap: 16px;
                }
                .left{
                    margin-right: 100px;
                    font-size: 26pt;
                    max-width: 60vw;
                    text-align: left;
                    margin-top: 40px;
                }
                .divider{
                    height: 4px;
                    width: 140px;
                    background: #ffff00;
                }
                .portrait{
                    height: auto;
                    width: 460px;
                    max-width: 100%;
                    border: 1px solid #9e9e9e;
                    border-radius: 100%;
                    //background: #ffff00;
                    margin: auto;
                    cursor: pointer;
                    -webkit-tap-highlight-color: transparent;
                    overflow: hidden;
                }
                .portrait .overlay{
                    height: 100%;
                    width: 100%;
                    background: #ffffff40;
                   backdrop-filter: blur(30px);
                   position: absolute;
                    z-index: 1;
                    display: none;
                    user-select: none;
                    outline: none;
                    -webkit-tap-highlight-color: transparent;
                }
                .portrait .overlay>*{
                    margin: auto;
                    color: #010101;
                    font-size: 1.9em;
                }
                @media (hover: hover) {
                    .portrait:hover .overlay{
                        display: grid;
                    }
                }
                .portrait img{
                    height: 100%;
                    width: 100%;
                    object-fit: cover;
                    height: auto;
                    transform: translate(-1%, -22%);
                }

                @media (max-width: 600px){
                    button{
                        margin: auto!important;
                    }
                    .navbar{
                        display:none;
                    }
                    .right{
                        grid-row-start: 1;
                    }
                    .left{
                        font-size: 1.3em;
                        margin: auto;
                        text-align: center;
                        max-width: 95vw;
                    }
                    .portrait{
                        width: 220px;
                        height: 220px;
                    }
                    .content{
                        grid-template-columns: auto;
                        text-align: center;
                        width: 80%;
                    }
                    .divider{
                        margin: auto;
                    }
                    .yob{
                        margin: auto;
                    }
                }

                @media (min-width: 600px){
                    .navbar{
                        display:none;
                    }
                    button{
                        margin: auto!important;
                    }
                    .left{
                        font-size: 1.3em;
                        margin: auto;
                        text-align: center;
                    }
                    .portrait{
                        width: 300px;
                        height: 300px;
                    }
                    .right{
                        grid-row-start: 1;
                    }
                    .content{
                        grid-template-columns: auto;
                        text-align: center;
                    }
                    .divider{
                        margin: auto;
                    }
                    .yob{
                        margin: auto;
                    }
                }
                @media (min-width: 768px){
                    .navbar{
                        display:block;
                    }
                    .left{
                        font-size: 1.4em;
                        text-align: left;
                    }  
                    .portrait{
                        width: 240px;
                        height: 240px;
                    }
                    .content{
                        grid-template-columns: auto auto;
                        text-align: left;
                    }
                    .right{
                        grid-column-start: 2;
                    }
                    .divider{
                        margin: 0;
                        margin-right: auto;
                    }
                    .yob{
                        margin: unset;
                    }
                }
                     
                @media (min-width: 992px){
                    .left{
                        font-size: 1.8em;
                    }  
                    .portrait{
                        width: 300px;
                        height: 300px;
                    }
                }
                @media (min-width: 1400px){
                    .left{
                        font-size: 2.6em;
                    }                                  
                    .portrait{
                        width: 400px;
                        height: 400px;
                    }
                }
            `}</style>
        </div >
    )
}
